.gallery-collection{
  padding: 0 1.75em;
  &__inner{
    padding: 8.75em 4.25em 6.25em;
    background-color: #35140a;
    border-radius: .625em;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 7.5em / 4));
    gap: 3.125em 2.5em;
  }
}

.gallery-item{
  @include imgCover(66.6%);
}

.invite{
  padding: 16.25em 0 17.5em;
  margin-top: 3.5em;
  position: relative;
  .h1{
    line-height: 120%;
  }
  &__bg,
  &__bg2{
    pointer-events: none;
    position: absolute;
  }
  &__bg{
    width: 102.5em;
    top: 0;
    left: 0;
  }
  &__bg2{
    width: 66em;
    top: 0;
    right: 0;
  }
  .info__inner{
    padding: 0;
  }
  &__nav{
    margin-top: 6em;
  }
  &__bg3,
  &__bg4{
    display: none;
  }
  ._hide-pc{
    display: none;
  }
}

.book{
  &__inner{
    overflow: hidden;
    padding: 6.25em 0;
    background-color: $white;
    box-shadow: $shadow;
  }
  &__nav{
    margin-top: 3.125em;
  }
  &__btn{
    width: 100%;
    max-width: 34.375em;
  }
}

.book-slider{
  width: 45.875em;
  max-width: 100%;
  margin: 3.5em auto 0;
  .swiper{
    &-container{
      width: calc(100% + 2.125em);
    }
    &-slide{
      padding-right: 2.125em;
      width: 100%;
    }
  }
}

.book-card{
  box-shadow: $shadow;
  &__img{
    @include imgCover(65.5%);
  }
}

.about{
  padding: 18.75em 8em 6.25em 20em;
  overflow: hidden;
  position: relative;
  &__img,
  &__img2{
    position: absolute;
    top: 0;
    pointer-events: none;
  }
  &__img{
    left: 0;
    width: 100%;
    display: block;
  }
  &__img2{
    display: none;
  }
  &__top{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.125em));
    gap: 6.25em;
    .h1{
      padding-left: .25em;
    }
    .info__item{
      margin-top: 6em;
    }
  }
  &__bottom{
    text-align: center;
    width: 79em;
    margin: 5em auto 0;
    padding: 5.5em 2em 0;
    border-top: 3px solid $secondary;
  }
}


@media screen and (max-width: 992px){
  .gallery-collection{
    padding: 0 .3125em;
    &__inner{
      padding: 1.75em 1.125em;
      border-radius: .3125em;
      grid-template-columns: repeat(3, calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }

  .invite{
    margin-top: 3.125em;
    padding: 6.75em 9em 6.25em;
    &__bg,
    &__bg2{
      font-size: .4375em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .book{
    &__inner{
      padding: 1.5em 0 1.75em;
    }
    .h1{
      font-size: 1.875em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      max-width: 10.75em;
      padding: 0;
    }
  }

  .book-slider{
    margin-top: 1.5em;
    width: 15.5em;
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        padding-right: .75em;
      }
    }
  }

  .about{
    padding: 6.5em 2em 2.5em 7em;
    &__top{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      .h1{
        padding-left: 0;
        font-size: 1.875em;
      }
      .info__item{
        margin-top: 2em;
      }
    }
    &__bottom{
      margin-top: 1em;
      padding-top: 1em;
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .gallery-collection{
    &__inner{
      grid-template-columns: repeat(2, calc(50% - .5em));
    }
  }

  .invite{
    padding: 3.75em 1.5em 7em;
    &__bg,
    &__bg2{
      display: none;
    }
    .h1{
      text-align: left;
      font-size: 1.875em;
      max-width: 9em;
    }
    .info__items{
      margin-top: .75em;
    }
    .info__item{
      padding-bottom: 6.875em;
      margin-bottom: 0;
      border: none;
      max-width: 17em;
      &:nth-child(2){
        max-width: 12em;
        margin-left: 4.75em;
        padding-bottom: 0;
      }
    }
    ._hide-pc{
      display: unset;
    }
    &__bg3,
    &__bg4{
      position: absolute;
      display: block;
      pointer-events: none;
    }
    &__bg3{
      top: 0;
      right: .5em;
      width: 21.5em;
    }
    &__bg4{
      bottom: -.5em;
      left: .5em;
      width: 24.0625em;
    }
    &__nav{
      margin-top: 1.5em;
      justify-content: unset;
      padding-left: 4.75em;
    }
    &__btn{
      padding: 0 1.875em;
    }
  }

  .about{
    padding: 1.5em 1.25em 5.5em;
    &__img{
      display: none;
    }
    &__img2{
      display: block;
      left: -1.25em;
      width: 5.125em;
    }
    &__top{
      grid-template-columns: 100%;
      gap: 1.5em;
      &-col,
      &-col2{
        position: relative;
        padding-left: 5.5em;
      }
      &-col{
        padding-bottom: 5.5em;
        border-bottom: 2px solid $secondary;
      }
    }
    &__bottom{
      display: none;
    }
    .info__item{
      max-width: 13.75em;
    }
  }
}
