.detail{
  padding: 5.5em 1em 6.25em;
  &__inner{
    padding: 6.25em 10.375em 6.25em 3.25em;
    box-shadow: $shadow;
    border-radius: .625em;
    background-color: $white;
    width: 96.75em;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 45.5em calc(100% - 45.5em - 3.125em);
    gap: 3.125em;
  }
  &__col2{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__bottom{
    width: 100%;
    max-width: 36.375em;
    margin: 1.5em 0 0 auto;
  }
  &__link{
    color: #56453f;
    img{
      width: 1.8125em;
      margin-right: 1.375em;
    }
    span{
      font-size: 1.5em;
    }
  }
  &__name{
    font-size: 3em;
    margin-top: .6em;
    border-bottom: 1px solid $dark;
  }
  &__title{
    font-size: 3em;
    line-height: 120%;
    font-family: $font2;
    font-weight: 900;
    margin-top: 1.5em;
  }
  &__nav{
    width: 100%;
    @include grid100gap(.875em);
    margin-top: 6em;
  }
  &__btn{
    height: 4.25em;
    border-radius: .625em;
    border-width: 3px;
  }
  &__params{
    margin-top: 1.5em;
    width: 100%;
  }
  &__subtitle2,
  &__desc{
    display: none;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 5em 36.375em;
  justify-content: space-between;
  .swiper{
    height: 48.125em;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .625em;
  }
  &__small{
    .gallery__img{
      cursor: pointer;
    }
    .swiper-slide{
      height: auto;
      padding-bottom: 1em;
      opacity: .5;
      &-thumb-active{
        opacity: 1;
      }
    }
  }
  &__big{
    padding-bottom: 1em;
    .gallery__img{
      border-radius: 0;
      height: 48.125em;
      padding-bottom: 0;
      img{
        //height: auto;
      }
    }
  }
}

.params-table{
  margin-top: 1.5em;
  color: $dark;
  table{
    width: 100%;
    border-spacing: 0;
  }
  td{
    height: 2.75em;
    text-align: center;
    border-top: 1px solid $primary;
    border-right: 1px solid $primary;
    &:first-child{
      border-left: 1px solid $primary;
    }
  }
  tr{
    &:first-child{
      td:first-child{
        border-top-left-radius: .625em;
      }
      td:last-child{
        border-top-right-radius: .625em;
      }
    }
    &:last-child{
      td{
        border-bottom: 1px solid $primary;
      }
      td:first-child{
        border-bottom-left-radius: .625em;
      }
      td:last-child{
        border-bottom-right-radius: .625em;
      }
    }
  }
  span{
    font-size: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .detail{
    padding: 1.5em 1.25em;
    &__inner{
      padding: 3em 1.5em 2.75em 1em;
      grid-template-columns: 19em calc(100% - 20.5em);
      gap: 1.5em;
    }
    &__bottom{
      max-width: 15.625em;
      margin-top: 0;
    }
    &__link{
      color: #56453f;
      img{
        width: .875em;
        margin-right: .6875em;
      }
      span{
        font-size: .75em;
      }
    }
    &__subtitle{
      display: none;
    }
    &__subtitle{
      display: none;
    }
    &__name{
      font-size: 1em;
    }
    &__title{
      font-size: 1.5em;
      margin-top: .7em;
    }
    &__nav{
      margin-top: 3.125em;
      gap: .625em;
    }
    &__btn{
      height: 2.375em;
      padding: 0;
      border-radius: .3125em;
      span{
        font-size: .875em;
      }
    }
  }

  .gallery{
    grid-template-columns: 2.375em 15.625em;
    .swiper{
      height: 20.5em;
    }
    &__img{
      border-radius: .3125em;
    }
    &__small{
      .swiper-slide{
        padding-bottom: .5em;
      }
    }
    &__big{
      .gallery__img{
        height: 20.5em;
      }
    }
  }

  .params-table{
    margin-top: .625em;
    td{
      height: 1.5em;
      span{
        font-size: .7em;
      }
    }
    tr{
      &:first-child{
        td:first-child{
          border-top-left-radius: .3125em;
        }
        td:last-child{
          border-top-right-radius: .3125em;
        }
      }
      &:last-child{
        td:first-child{
          border-bottom-left-radius: .3125em;
        }
        td:last-child{
          border-bottom-right-radius: .3125em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .detail{
    &__inner{
      grid-template-columns: 100%;
      position: relative;
    }
    &__col2{
      text-align: center;
      align-items: center;
      padding-left: .5em;
    }
    &__nav{
      width: 10.75em;
    }
    &__desc{
      display: block;
      margin-top: 1.5em;
      width: 17em;
    }
    &__subtitle2{
      position: absolute;
      top: 1.25em;
      right: 1.5em;
      display: block;
      color: #88372d;
      width: 15.625em;
    }
  }
}
