.main{
  position: relative;
  min-height: 47em;
  color: $white;
  padding: 8.75em 0;
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 1.875em;
    background-color: $brown;
    z-index: 2;
  }
  &:before{
    top: 0;
  }
  &:after{
    bottom: 0;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  .small-container{
    position: relative;
    z-index: 2;
  }
  &__inner{

  }
  &__text{
    max-width: 47em;
  }
  &__title{
    margin: .25em 0;
  }
  &__nav{
    margin-top: 2.5em;
  }
}

.info{
  &__inner{
    padding: 9em 0;
    position: relative;
  }
  .h1{
    line-height: 120%;
  }
  &__items{
    margin-top: 4em;
    text-align: center;
  }
  &__item{
    padding-bottom: 8em;
    margin-bottom: 5em;
    border-bottom: 3px solid $secondary;
    &:last-child{
      border-bottom: none;
      padding-bottom: 0;
      margin-bottom: 0;
    }
    &-text{
      color: #56453f;
      &:first-letter{
        color: $secondary;
        font-weight: 900;
        font-style: italic;
        font-family: $font2;
        font-size: 2.5em;
      }
      li{
        line-height: 120%;
      }
      ul{
        margin-top: 1em;
        li{
          &:before{
            content: '•';
          }
        }
      }
    }
  }
  &__img{
    display: none;
  }
}

.info-props{
  position: absolute;
  top: -3.75em;
  left: 0;
  width: 100%;
  padding: .375em 11em;
  min-height: 5.75em;
  box-shadow: $shadow;
  background-color: $white;
  border-radius: .3125em;
  z-index: 4;
  &__item{
    text-align: center;
    img{
      max-width: 2.9375em;
      max-height: 2.9375em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
    span{
      line-height: 88%;
    }
  }
}

.info-props2{
  padding-left: 5.75em;
  padding-right: 5.75em;
  top: unset;
  bottom: -2.75em;
}


@media screen and (max-width: 992px){
  .main{
    padding: 12em 1.25em 8.125em;
    margin-top: -.5em;
    min-height: 35em;
    &__title{
      margin: .125em 0;
    }
  }

  .info{
    padding: 0 1.25em;
    &__inner{
      padding: 7em 0 3em;
    }
    .h1{
      font-size: 1.875em;
      text-align: left;
    }
    &__items{
      margin-top: 2em;
      text-align: left;
    }
    &__item{
      padding-bottom: 2.25em;
      margin-bottom: 2.25em;
      border-width: 2px;
      color: #56453f;
      &-text{
        &:first-letter{
          font-size: 1.875em;
        }
      }
    }
  }


  .info-props{
    top: -1.75em;
    padding: .25em 2em;
    background-color: $white;
    min-height: 2.5em;
    &__item{
      img{
        max-width: 1em;
        max-height: 1em;
        margin-right: .5em;
      }
    }
  }

  .info-props2{
    top: unset;
    bottom: -1.25em;
    padding-right: 1.25em;
    padding-left: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .main{
    &:before,
    &:after{
      height: .9375em;
    }
    &__bg{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .info{
    &__inner{
      position: relative;
    }
    &__img{
      display: block;
      position: absolute;
      top: -.125em;
      left: 0;
      width: 100%;
      z-index: 5;
    }
  }
}
