.footer,
.header{
  position: relative;
  z-index: 4;
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .footer,
  .header{
    padding-top: 1.75em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
