@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 6.25em;
    height: 4.4375em;
    border-radius: .3125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.5em;
    }
}

.m-btn-orange{
    background-color: $secondary;
    color: $white;
    &:hover{
        background-color: darken($secondary,10%);
        color: $white;
    }
}

.m-btn-brown{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-brown-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.375em;
        padding: 0 3em;
        span{
            font-size: .875em;
        }
    }
}


